import React, { useState, useContext, useEffect } from "react"

import { AuthContext } from "@meddbriefer/mdb-auth"

const defaultPrefs = {
    dispLeafNodesInline: false,
    dispAssessmentFindingsInline: true,
    inhibitRaterOversharing: true
}

const noop = () => {}

export const PreferencesContext = React.createContext({
    ...defaultPrefs,
    setPref: noop,
    getCurrentLayout: noop,
    setCurrentLayout: noop,
    isCurrentLayout: noop
})


export const PreferencesProvider = (props) => {
    const { isAuthenticated, getUserData, saveUserData, setUserPrefs } = useContext(AuthContext)
    const [dispLeafNodesInline, setDispLeafNodesInline] = useState(defaultPrefs.dispLeafNodesInline)
    const [dispAssessmentFindingsInline, setDispAssessmentFindingsInline] = useState(defaultPrefs.dispAssessmentFindingsInline)
    const [inhibitRaterOversharing, setInhibitRaterOversharing] = useState(defaultPrefs.inhibitRaterOversharing)
    const [layout, setLayout] = useState("layout1")


    const getCurrentLayout = () => layout
    const isCurrentLayout = (layoutName) => layoutName === layout

    const setPref = (name, value) => {
        switch(name) {
            case "dispLeafNodesInline":
                setDispLeafNodesInline(value)
                break
            case "dispAssessmentFindingsInline":
                setDispAssessmentFindingsInline(value)
                break;
            case "inhibitRaterOversharing":
                setInhibitRaterOversharing(value)
                break
            default:
                console.log(`not setting unknown pref ${name} to ${value}`)
        }
    }

    // On logged-in useEffect
    // useEffect(() => {
    //     // Get user data and apply if logged in
    //     isAuthenticated &&
    //     getUserData()
    //         .then(res => {
    //             // console.log(res);
    //             setCurrentLayout(res.preferences.layout);
    //         })
    //         .catch(err => console.log(err));
    // }, [isAuthenticated]);

    // Pass preferences to auth for DB sync
    useEffect(() => {
        // Preferences data to upload
        let data = {
            dispLeafNodesInline,
            dispAssessmentFindingsInline,
            inhibitRaterOversharing,
            layout
        }

        // Update preferences and save if logged in
        setUserPrefs({data});
        isAuthenticated &&
            saveUserData("preferences", data);

    }, [layout, dispLeafNodesInline, dispAssessmentFindingsInline, inhibitRaterOversharing]);

    const setCurrentLayout = (layout) => {
        switch(layout) {
            case "layout1":
                setDispLeafNodesInline(false)
                setLayout("layout1")
                break
            default:
                // set to layout2 no matter what was passed in
                setDispLeafNodesInline(true)
                setLayout("layout2")
                break
        }
    }

    return (
        <PreferencesContext.Provider
            value={{
                dispLeafNodesInline,
                dispAssessmentFindingsInline,
                inhibitRaterOversharing,
                setPref,
                getCurrentLayout,
                setCurrentLayout,
                isCurrentLayout
            }}
        >
            {props.children}
        </PreferencesContext.Provider>
    )
}

export const usePrefs = () => useContext(PreferencesContext)
