import React, { useContext, useEffect } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import { FormCheckBox, RequiredPromptField } from "./FormUtils"


const ControlBleeding = () => {
    const {
        allChecked,
        buildDepListFromCheckListItemIDs,
        setDerivedCheckListItemValue,
        someChecked,
        shouldDisplayInterventionForm,
        toggleChecked,
        toggleDisplayInterventionForm,
    } = useContext(ScenarioContext)

    const CONTROL_BLEEDING_DEPS = [
        "intv-control-bleeding-direct-pressure",
        "intv-control-bleeding-tourniquet",
        "intv-control-bleeding-2nd-tourniquet",
        "intv-control-bleeding-pressure-bandage",
        "intv-control-bleeding-hemostatic-gauze",
        "intv-control-bleeding-pack-wound-with-gauze",
        "intv-control-bleeding-load-and-go"
    ]
    const TOURN_PLACEMENT_DEPS = [
        "intv-control-bleeding-tourniquet-placement-close-to-core-far-from-wound",
        "intv-control-bleeding-tourniquet-placement-other"
    ]
    const TOURN_STOP_TIGHTENING_DEPS = [
        "intv-control-bleeding-tourniquet-stop-tightening-when-bleeding-has-stopped",
        "intv-control-bleeding-tourniquet-stop-tightening-when-distal-pulse-is-absent",
        "intv-control-bleeding-tourniquet-stop-tightening-other"
    ]

    const TOURN_DEPS = [
        "intv-control-bleeding-tourniquet-placement",
        "intv-control-bleeding-tourniquet-when-to-stop-tightening"
    ]

    const TOURN_2_PLACEMENT_DEPS = [
        "intv-control-bleeding-2nd-tourniquet-placement-adjacent-to-initial-tourniquet",
        "intv-control-bleeding-2nd-tourniquet-placement-other"
    ]

    useEffect(
        () => { setDerivedCheckListItemValue("intv-control-bleeding-tourniquet", "all", TOURN_DEPS)},
        [ buildDepListFromCheckListItemIDs(TOURN_DEPS) ]
    )

    useEffect(
        () => { setDerivedCheckListItemValue("intv-control-bleeding-2nd-tourniquet", "some", TOURN_2_PLACEMENT_DEPS)},
        [ buildDepListFromCheckListItemIDs(TOURN_2_PLACEMENT_DEPS)]
    )

    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="control-bleeding"
                    label="What methods of bleeding control are you using, and in what order?"
                    checkedWhen="some"
                    options={CONTROL_BLEEDING_DEPS}
                />
                <ul>
                    <li><FormCheckBox id="intv-control-bleeding-direct-pressure"       label="Apply direct pressure"         clickHandler={toggleChecked} /></li>
                    <li>
                        <FormCheckBox id="intv-control-bleeding-tourniquet"            label="Apply tourniquet"              clickHandler={toggleDisplayInterventionForm} />
                        {shouldDisplayInterventionForm("intv-control-bleeding-tourniquet") && (
                        <ul>
                            <li>
                                <RequiredPromptField
                                    id="intv-control-bleeding-tourniquet-placement"
                                    label="Where would you place the tourniquet?"
                                    checkedWhen="some"
                                    options={TOURN_PLACEMENT_DEPS}
                                />
                                <ul>
                                    <li><FormCheckBox id="intv-control-bleeding-tourniquet-placement-close-to-core-far-from-wound" label="As far from wound and close to the core as possible" clickHandler={toggleChecked} /></li>
                                    <li><FormCheckBox id="intv-control-bleeding-tourniquet-placement-other" label="Other" clickHandler={toggleChecked} /></li>
                                </ul>
                            </li>
                            <li>
                                <RequiredPromptField
                                    id="intv-control-bleeding-tourniquet-when-to-stop-tightening"
                                    label="When do you know when to stop tightening the tourniquet?"
                                    checkedWhen="some"
                                    options={TOURN_STOP_TIGHTENING_DEPS}
                                />
                                <ul>
                                    <li><FormCheckBox id="intv-control-bleeding-tourniquet-stop-tightening-when-bleeding-has-stopped" label="When bleeding has stopped" clickHandler={toggleChecked} /></li>
                                    <li><FormCheckBox id="intv-control-bleeding-tourniquet-stop-tightening-when-distal-pulse-is-absent" label="When distal pulse is absent" clickHandler={toggleChecked} /></li>
                                    <li><FormCheckBox id="intv-control-bleeding-tourniquet-stop-tightening-other" label="Other" clickHandler={toggleChecked} /></li>
                                </ul>
                            </li>

                        </ul>
                        )}
                    </li>
                    <li>
                        <FormCheckBox id="intv-control-bleeding-2nd-tourniquet"        label="Apply second tourniquet"       clickHandler={toggleDisplayInterventionForm} />
                        {shouldDisplayInterventionForm("intv-control-bleeding-2nd-tourniquet") && (
                        <ul>
                            <li>
                                <i> Where would you place the 2nd tourniquet?</i>
                                <ul>
                                    <li><FormCheckBox id="intv-control-bleeding-2nd-tourniquet-placement-adjacent-to-initial-tourniquet" label="Adjacent to initial tourniquet" clickHandler={toggleChecked} /></li>
                                    <li><FormCheckBox id="intv-control-bleeding-2nd-tourniquet-placement-other" label="Other" clickHandler={toggleChecked} /></li>
                                </ul>
                            </li>
                        </ul>
                        )}

                    </li>
                    <li><FormCheckBox id="intv-control-bleeding-pressure-bandage"      label="Apply pressure bandage"        clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-control-bleeding-hemostatic-gauze"      label="Apply hemostatic gauze"        clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-control-bleeding-pack-wound-with-gauze" label="Pack wound with gauze"         clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-control-bleeding-load-and-go"           label="Load and go (if uncontrolled)" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
        </ul>
    )
}


export default ControlBleeding
