import React from "react"

import { IntvForm } from "./FormUtils"


const RestrictSpinalMotion = () => {

    const FORM_DATA = [
        {
            id: "intv-restricts-spinal-motion",
            type: "required-prompt-field",
            prompt: "What spinal immobilization techniques will you apply, and in which order?",
            checkedWhen: "some",
            children: [
                { id: "intv-spinal-immobilization-manual-c-spine", type: "checkbox", label: "Manual c-spine stabilization" },
                { id: "intv-spinal-immobilization-ked", type: "checkbox", label: "Seated spinal motion restriction (KED)" },
                { id: "intv-spinal-immobilization-lbb", type: "checkbox", label: "Supine long back board (LBB)" },
                { id: "intv-spinal-immobilization-cervical-collar", type: "checkbox", label: "Cervical collar" },
                { id: "intv-spinal-immobilization-blocks", type: "checkbox", label: "Cervical immobilization device (blocks)" }
            ]
        }
    ]

    return (
        <IntvForm data={FORM_DATA} />
    )

}


export default RestrictSpinalMotion