
import React, { useState, useContext } from "react"

import {
    Nav,
    NavItem,
    NavLink,
    TabContent,
    TabPane,
} from "reactstrap"

import classnames from "classnames"

// import { usePrefs } from "../contexts/PreferencesContext"
import { ScenarioContext } from "../contexts/ScenarioContext"

import AssessmentFindingsTabContent from "./tabs/AssessmentFindings"
import InterventionsTabContent from "./tabs/InterventionsTab"
import SampleTabContent from "./tabs/SampleTab"
import VitalsTabContent from "./tabs/VitalsTab"

export default function RaterInfo({defaultTab = "interventions"}) {
    // const {
    //     checkListItems,
    //     isChecked,
    //     scenario,
    // } = useContext(ScenarioContext)

    const [activeTab, setActiveTab] = useState(defaultTab)
    const activateTab = (tab) => {
        if (activeTab !== tab) setActiveTab(tab)
    }
    const isActiveTab = (tab) => tab === activeTab

    return (
        <>
            <Nav tabs>
                <NavItem>
                    <NavLink
                        className={classnames({ active: isActiveTab('interventions')})}
                        onClick={() => activateTab("interventions")}
                    >
                        Interventions
                    </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink
                        className={classnames({ active: isActiveTab('vitals') })}
                        onClick={() => activateTab('vitals')}
                    >
                        Vitals
                    </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink
                        className={classnames({ active: isActiveTab('sample') })}
                        onClick={() => activateTab('sample')}
                    >
                        SAMPLE
                    </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink
                        className={classnames({ active: isActiveTab('callouts') })}
                        onClick={() => activateTab('callouts')}
                    >
                        Assessment Findings
                    </NavLink>
                </NavItem>
            </Nav>
            <TabContent activeTab={activeTab}>
                <TabPane tabId="interventions">
                    <InterventionsTabContent />
                </TabPane>
                <TabPane tabId="vitals" >
                    <VitalsTabContent />
                </TabPane>
                <TabPane tabId="sample" >
                    <SampleTabContent />
                </TabPane >
                <TabPane tabId="callouts" >
                    <AssessmentFindingsTabContent />
                </TabPane>
            </TabContent>
        </>
    );
}