import React, { useContext, useEffect } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import { FormCheckBox, RequiredPromptField } from "./FormUtils"

const OxygenTherapy = () => {
    const {
        buildDepListFromCheckListItemIDs,
        setDerivedCheckListItemValue,
        someChecked,
        toggleChecked
    } = useContext(ScenarioContext)

    const DEVICE_DEPS = [
        "intv-oxygen-therapy-device-non-rebreather-mask",
        "intv-oxygen-therapy-device-nasal-cannula"
    ]

    const FLOW_RATE_DEPS = [
        "intv-oxygen-therapy-flow-rate-2-6-lpm",
        "intv-oxygen-therapy-flow-rate-15-lpm",
        "intv-oxygen-therapy-flow-rate-other"
    ]

    const TARGET_SATURATION_DEPS = [
        "intv-oxygen-therapy-target-saturation-94-or-greater",
        "intv-oxygen-therapy-target-saturation-other"
    ]

    const OXYGEN_THERAPY_DEPS = [
        "intv-oxygen-therapy-device",
        "intv-oxygen-therapy-flow-rate",
        "intv-oxygen-therapy-target-saturation"
    ]

    useEffect(
        () => { setDerivedCheckListItemValue("oxygen-therapy", "all", OXYGEN_THERAPY_DEPS) },
        [ buildDepListFromCheckListItemIDs(OXYGEN_THERAPY_DEPS) ]
    )

    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="intv-oxygen-therapy-device"
                    label="What device will you use to deliver supplemental oxygen?"
                    checkedWhen="some"
                    options={DEVICE_DEPS}
                />
                <ul>
                    <li><FormCheckBox id="intv-oxygen-therapy-device-non-rebreather-mask" label="Non-rebreather mask" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-oxygen-therapy-device-nasal-cannula" label="Nasal cannula" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
            <li>
                <RequiredPromptField
                    id="intv-oxygen-therapy-flow-rate"
                    label="Oxygen flow rate?"
                    checkedWhen="some"
                    options={FLOW_RATE_DEPS}
                />
                <ul>
                    <li><FormCheckBox id="intv-oxygen-therapy-flow-rate-2-6-lpm" label="2-6 liters/min" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-oxygen-therapy-flow-rate-15-lpm" label="15 liters/min" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-oxygen-therapy-flow-rate-other" label="Other" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
            <li>
                <RequiredPromptField
                    id="intv-oxygen-therapy-target-saturation"
                    label="What is your target oxygen saturation range?"
                    checkedWhen="some"
                    options={TARGET_SATURATION_DEPS}
                />
                <ul>
                    <li><FormCheckBox id="intv-oxygen-therapy-target-saturation-94-or-greater" label="94 or greater" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-oxygen-therapy-target-saturation-other" label="Other" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
        </ul>
    )
}

export default OxygenTherapy