import React, {useContext } from "react"

import {ScenarioContext} from "../../contexts/ScenarioContext"

import RevealTable from "../RevealTable"

const VitalsTabContent = () => {
    const {
        scenario,
        currentVitals,
        vitalsRecomputed
    } = useContext(ScenarioContext)
    // vitals tab will display either 2 or 3 columns, depending on
    // whether vitals have been recomputed, and thus we need to display
    // both the initial and current value
    const vitalsHeaders =
        vitalsRecomputed
            ? ["Vital Sign", "Initial Value", "Current Value"]
            : ["Vital Sign", "Current Value"]

    const vitals = Object.entries(scenario.vitalSigns).map(iv => {
        const value =
            vitalsRecomputed
                ? [iv[1].initial, currentVitals[iv[0]]]
                : [iv[1].initial]

        return {
            label: iv[0],
            value: value
        }
    })

    return (
            <RevealTable
                title="Vitals"
                headings={vitalsHeaders}
                rows={vitals}
            />
    )
}

export default VitalsTabContent