import React, { useContext } from "react";

import classnames from "classnames";

import { ScenarioContext } from "../contexts/ScenarioContext"


const CallOutText = ({ step }) => {

  const {scenario, isCurrentAssessmentFinding} = useContext(ScenarioContext)

  return  (
    <span className={classnames("callout", {currentCallOut: isCurrentAssessmentFinding(step.id)})}>
      {scenario.assessmentFindings[step.id]}
    </span>
  )

}

export default CallOutText;