import React, { useContext, useEffect } from "react"

import { ScenarioContext } from "../../contexts/ScenarioContext"

import { IntvForm, FormCheckBox, RequiredPromptField } from "./FormUtils"

const IV = () => {

    const {checkListItems, setDerivedCheckListItemValue} = useContext(ScenarioContext)
    const FORM_DATA = [
        {
            id: "intv-control-shock-num-ivs",
            type: "drop-down-menu",
            prompt: "How many IVs would you establish?",
            children: [
                { id: "intv-control-shock-num-ivs-one", label: "one" },
                { id: "intv-control-shock-num-ivs-two", label: "two" },
                { id: "intv-control-shock-num-ivs-other", label: "Other" },
            ]
        },
        {
            id: "intv-control-shock-iv-size",
            type: "drop-down-menu",
            prompt: "What size IV?",
            children: [
                { id: "intv-control-shock-iv-size-large-as-possible", label: "As large as possible" },
                { id: "intv-control-shock-iv-size-other", label: "Other" }
            ]
        },
        {
            id: "intv-control-shock-iv-fluid",
            type: "drop-down-menu",
            prompt: "What type of fluid?",
            children: [
                { id: "intv-control-shock-iv-fluid-saline", label: "Normal Saline" },
                { id: "intv-control-shock-iv-fluid-other", label: "Other" }
            ]
        },
        {
            id: "intv-control-shock-iv-volume",
            type: "drop-down-menu",
            prompt: "At what volume?",
            children: [
                { id: "intv-control-shock-iv-volume-20-cc-per-kilo", label: "20 cc/kilo" },
                { id: "intv-control-shock-iv-volume-other", label: "Other" }
            ]
        },
        {
            id: "intv-control-shock-iv-target-bp",
            type: "drop-down-menu",
            prompt: "What is your target BP?",
            children: [
                { id: "intv-control-shock-iv-target-bp-80-90", label: "SBP 80-90, if bleeding is controlled" },
                { id: "intv-control-shock-iv-target-bp-other", label: "Other" }
            ]
        }
    ]

    const IV_DEPS = [
        "intv-control-shock-num-ivs",
        "intv-control-shock-iv-size",
        "intv-control-shock-iv-fluid",
        "intv-control-shock-iv-volume",
        "intv-control-shock-iv-target-bp"
    ]

    useEffect(
        () => {
            setDerivedCheckListItemValue("intv-control-shock-administer-iv-boluses", "all", IV_DEPS)
        },
        // [buildDepListFromCheckListItemIDs(IV_DEPS)]
        [checkListItems]
    )

    return (
        <IntvForm data={FORM_DATA} />
    )

}

const ControlShock = () => {
    const {
        buildDepListFromCheckListItemIDs,
        setDerivedCheckListItemValue,
        someChecked,
        shouldDisplayInterventionForm,
        toggleChecked,
        toggleDisplayInterventionForm
    } = useContext(ScenarioContext)

    const CONTROL_SHOCK_DEPS = [
        "intv-control-shock-keep-patient-warm",
        "intv-control-shock-administer-high-concentration-o2",
        "intv-control-shock-place-patient-supine-position",
        "intv-control-shock-rapid-transport",
        "intv-control-shock-administer-iv-boluses"
    ]



    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="control-shock"
                    label="What techniques will you use to manage shock?"
                    checkedWhen="some"
                    options={CONTROL_SHOCK_DEPS}
                />
                <ul>
                    <li><FormCheckBox id="intv-control-shock-keep-patient-warm"                label="Keep patient warm (e.g., apply blankets)" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-control-shock-administer-high-concentration-o2" label="Administer high concentration of oxygen" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-control-shock-place-patient-supine-position"    label="Place or maintain patient in supine position" clickHandler={toggleChecked} /></li>
                    <li>
                        <FormCheckBox id="intv-control-shock-administer-iv-boluses" label="Administer IV fluid resuscitation" clickHandler={toggleDisplayInterventionForm} />
                        {shouldDisplayInterventionForm("intv-control-shock-administer-iv-boluses") && (
                            <IV />
                        )}
                    </li>
                </ul>
            </li>
        </ul>
    )
}


export default ControlShock