const ManagePain = () => {
    const {
        buildDepListFromCheckListItemIDs,
        someChecked,
        setDerivedCheckListItemValue,
        shouldDisplayInterventionForm,
        toggleChecked,
        toggleDisplayInterventionForm,
    } = useContext(ScenarioContext)

    const MANAGE_PAIN_MED_DEPS = [
        "intv-pain-management-medication-option-1",
        "intv-pain-management-medication-option-2",
        "intv-pain-management-medication-option-3",
        "intv-pain-management-medication-option-other",
    ]

    const MANAGE_PAIN_MIN_BP_DEPS = [
        "intv-pain-management-medication-min-bp-correct",
        "intv-pain-management-medication-min-bp-other"
    ]

    const MANAGE_PAIN_DOSAGE_DEPS = [
        "intv-pain-management-dosage-scenario-specific",
        "intv-pain-management-dosage-other"
    ]

    const MANAGE_PAIN_ROUTE_DEPS = [
        "intv-pain-management-administration-route-oral",
        "intv-pain-management-administration-route-intranasal",
        "intv-pain-management-administration-route-intraosseous",
        "intv-pain-management-administration-route-intramuscular",
        "intv-pain-management-administration-route-intravenous"
    ]

    const MANAGE_PAIN_DEPS = [
        "intv-pain-management-medication",
        "intv-pain-management-medication-min-bp",
        "intv-pain-management-medication-dosage",
        "intv-pain-management-medication-route"
    ]

    useEffect(() => {
            setDerivedCheckListItemValue("intv-additional-manage-pain", "all", MANAGE_PAIN_DEPS)
        },
        buildDepListFromCheckListItemIDs(MANAGE_PAIN_DEPS)
    )

    return (
        <ul>
            <li>
                <RequiredPromptField
                    id="intv-pain-management-medication"
                    label="What medication(s) would you administer?"
                    checkedWhen="some"
                    options={MANAGE_PAIN_MED_DEPS}
                />
                <ul>
                    <li><FormCheckBox id="intv-pain-management-medication-option-1" label="option-1" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-pain-management-medication-option-2" label="option-2" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-pain-management-medication-option-3" label="option-3" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-pain-management-medication-option-other" label="Other" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
            <li>
                <RequiredPromptField
                    id="intv-pain-management-medication-min-bp"
                    label="What is the minimum BP to administer pain medications?"
                    checkedWhen="some"
                    options={MANAGE_PAIN_MIN_BP_DEPS}
                />
                <ul>
                    <li><FormCheckBox id="intv-pain-management-medication-min-bp-correct" label="correct-answer" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-pain-management-medication-min-bp-other" label="Other" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
            <li>
                <RequiredPromptField
                    id="intv-pain-management-medication-dosage"
                    label="What dosage?"
                    checkedWhen="some"
                    options={MANAGE_PAIN_DOSAGE_DEPS}
                />
                <ul>
                    <li><FormCheckBox id="intv-pain-management-dosage-scenario-specific" label="scenario-specific-option" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-pain-management-dosage-other" label="Other" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
            <li>
                <RequiredPromptField
                    id="intv-pain-management-medication-route"
                    label="How would you administer this medication--through what route?"
                    checkedWhen="some"
                    options={MANAGE_PAIN_ROUTE_DEPS}
                />
                <ul>
                    <li><FormCheckBox id="intv-pain-management-administration-route-oral"          label="Oral" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-pain-management-administration-route-intranasal"    label="Intranasal" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-pain-management-administration-route-intraosseous"  label="Intraosseous" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-pain-management-administration-route-intramuscular" label="Intramuscular" clickHandler={toggleChecked} /></li>
                    <li>
                        <FormCheckBox id="intv-pain-management-administration-route-intravenous" label="Intravenous" clickHandler={toggleDisplayInterventionForm}/>
                        {shouldDisplayInterventionForm("intv-pain-management-administration-route-intravenous") && (
                            <IntravenousRoute />
                        )}
                    </li>
                </ul>
            </li>

        </ul>)

}


const AdministerAdditionalInterventions = () => {
    const {
        buildDepListFromCheckListItemIDs,
        setDerivedCheckListItemValue,
        shouldDisplayInterventionForm,
        toggleChecked,
        toggleDisplayInterventionForm,
    } = useContext(ScenarioContext)

    const ADDTL_INTV_DEPS = [
        "intv-additional-estabilish-2-large-bore-ivs",
        "intv-additional-apply-bandages",
        "intv-additional-administer-iv",
        "intv-additional-manage-pain",
        "intv-additional-other"
    ]

    useEffect(
        () => { setDerivedCheckListItemValue("administer-additional-interventions", "some", ADDTL_INTV_DEPS)},
        buildDepListFromCheckListItemIDs(ADDTL_INTV_DEPS)
    )
    return (
        <ul>
            <li><i>What additional interventions will you perform?</i>
                <ul>
                    <li><FormCheckBox id="intv-additional-estabilish-2-large-bore-ivs" label="Establish two large bore IVs" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-additional-apply-bandages" label="Apply bandages" clickHandler={toggleChecked} /></li>
                    <li><FormCheckBox id="intv-additional-administer-iv" label="Administer IV" clickHandler={toggleChecked} /></li>
                    <li>
                        <FormCheckBox id="intv-additional-manage-pain" label="Manage pain/nausea" clickHandler={toggleDisplayInterventionForm} />
                        {shouldDisplayInterventionForm("intv-additional-manage-pain") && (
                            <ManagePain />
                        )}
                    </li>
                    <li><FormCheckBox id="intv-additional-other" label="Other" clickHandler={toggleChecked} /></li>
                </ul>
            </li>
        </ul>
    )
}

export default AdministerAdditionalInterventions
