import React, { useContext, useEffect, useState, useMemo, useDebugValue} from "react"

import classnames from "classnames"

import {ScenarioContext} from "../../contexts/ScenarioContext"


export const RequiredPromptField = ({ id, label, checkedWhen, options=[] }) => {
    // , checked
    const { buildDepListFromCheckListItemIDs, checkListItems, setDerivedCheckListItemValue } = useContext(ScenarioContext)
    // const [deps, setDeps] = useState(buildDepListFromCheckListItemIDs(options))
    // const deps = buildDepListFromCheckListItemIDs(options)
    // useEffect(
    //     () => {
    //         setChecked(id, checked)
    //     },
    //     [checked]
    // )
    useEffect(
        () => {
            setDerivedCheckListItemValue(id, checkedWhen, options)
        },
        // [ buildDepListFromCheckListItemIDs(options) ]
        [ checkListItems ]
    )
    return (
        <i>{label}</i>
    )
}

export const NestedForm = ({id, label, checkedWhen, children}) => {
    const {checkListItems, setDerivedCheckListItemValue, shouldDisplayInterventionForm, toggleDisplayInterventionForm} = useContext(ScenarioContext)
    const deps = useMemo(() => children.map(child => child.id), [...children])
    useEffect(
        () => {setDerivedCheckListItemValue(id, checkedWhen, deps)},
        [ checkListItems ]
    )

    return (
        <ul>
            <li>
                <FormCheckBox
                    id={id}
                    label={label}
                    clickHandler={toggleDisplayInterventionForm}
                />
                {shouldDisplayInterventionForm(id) && (
                    <ul>
                        {children.map(child => {
                            <li key={child.id}><IntvFormItem item={child}/></li>
                        })}
                    </ul>
                )}

            </li>
        </ul>
    )
}
export const DerivedValue = ({id, checkedWhen, children}) => {

    const {checkListItems, setDerivedCheckListItemValue } = useContext(ScenarioContext)
    const deps = useMemo(() => children.map(child => child.id), [...children])

    // set the derived value on the initial render
    useEffect(
        () => { setDerivedCheckListItemValue(id, checkedWhen, deps) },
        []
    )

    // set the derived value when something changes
    useEffect(
        () => {setDerivedCheckListItemValue(id, checkedWhen, deps)},
        [checkListItems]
    )

    return (
        <>
            {children.map(child =>
                <IntvFormItem item={child} />
            )}
        </>
    )
}


export const FormCheckBox = ({ id, label, clickHandler }) => {
    const { isChecked } = useContext(ScenarioContext)

    const whichIcon = isChecked(id) ? "check_box" : "check_box_outline_blank"

    return (
        <span onClick={() => clickHandler(id)}>
            <span
                className="checkbox-icon material-icons-outlined"
            >
                {whichIcon}
            </span>
            <span>
                {label}
            </span>
        </span>
    )
}

export const DropDownMenu = ({ id, label, children }) => {

    const { checkListItems, isChecked, setChecked, setDerivedCheckListItemValue } = useContext(ScenarioContext)
    const deps = useMemo(() => children.map(child => child.id), [...children])

    useEffect(
        () => { setDerivedCheckListItemValue(id, "some", deps) },
        []
    )

    // set the derived value when something changes
    useEffect(
        () => { setDerivedCheckListItemValue(id, "some", deps) },
        [checkListItems]
    )

    const changeHandler = (event) => {
        // FIXME: we need to setChecked(false) when an item is de-selected. perhaps useState()?
        setChecked(event.target.value, true)
        // setChecked(id, true)
    }

    // console.log(children)
    return (
        <li>
            <fieldset>
                <legend>
                    <span className={classnames("material-icons-outlined", { "invisible": !isChecked(id) })}>
                        check
                    </span>
                    <i>{label}</i>
                </legend>

            <select
                id={id}
                onChange={changeHandler}
                className="form-control"
            >
                <option value="" disabled>----</option>
                {children.map(child => (
                    <option key={child.id} value={child.id}>{child.label}</option>
                ))}
            </select>
            </fieldset>
        </li>
    )
}

export const SelectOneOrMore = ({id, label, checkedWhen, children}) => {

    const {checkListItems, isChecked, setDerivedCheckListItemValue} = useContext(ScenarioContext)

    const deps = useMemo(() => children.map(child => child.id), [...children])

    // set the derived value on initial render
    useEffect(
        () => { setDerivedCheckListItemValue(id, checkedWhen, deps) },
        []
    )

    // set the derived value when something changes
    useEffect(
        () => {setDerivedCheckListItemValue(id, checkedWhen, deps)},
        [checkListItems]
    )

    return (
        <li>
            <fieldset>
                <legend>
                    <span className={classnames("material-icons-outlined", { "invisible": !isChecked(id) })}>
                        check
                    </span>
                    <i>{label}</i>
                </legend>
                <ul>
                    {children.map((child) =>
                        <li key={child.id}><IntvFormItem item={child} /></li>
                    )}
                </ul>
            </fieldset>
        </li>
    )
}


export const IntvFormItem = ({ item }) => {
    const { toggleChecked, someChecked } = useContext(ScenarioContext)

    switch (item.type) {
        case "derived-value":
            return (
                <DerivedValue
                    id={item.id}
                    checkedWhen={item.checkedWhen}
                    children={item.children}
                />
            )
        case "select-one-or-more":
            return (
                <SelectOneOrMore
                    id={item.id}
                    label={item.prompt}
                    checkedWhen={item.checkedWhen}
                    children={item.children}
                />
            )
        case "required-prompt-field":

            const deps = item.children.map((child) => child.id)

            return (
                <li>
                    <fieldset>
                        <legend>
                            <RequiredPromptField
                                id={item.id}
                                label={item.prompt}
                                checkedWhen={item.checkedWhen}
                                options={deps}
                            />
                        </legend>
                        <ul>
                            {item.children.map((child) =>
                                <li key={child.id}><IntvFormItem item={child} /></li>
                            )}
                        </ul>
                    </fieldset>
                </li>
            )
        case "drop-down-menu":
            return (<DropDownMenu id={item.id} label={item.prompt} children={item.children} />)
        case "checkbox":
            return (<FormCheckBox id={item.id} label={item.label} clickHandler={toggleChecked} />)
        case "prompt":
            return (<li><i>{item.label}</i></li>)
        default:
            return (<p>placeholder for unhandled item type</p>)
    }
}

export const IntvForm = ({ data }) => {
    return (
        <ul>
            {data.map((rec) =>
                <IntvFormItem key={rec.id} item={rec} />
            )}
        </ul>
    )
}
